<template>
	<view class="h-screen py-2 bg-white"> 
		<div class="text-gray-800 text-xl text-center">
			<div v-if="data && data.title">{{data.title}}</div>
		</div>
		<div class="py-2 px-4 ">
				<content v-if="data && data.content" :data="{data:data.content}" child="a" />
				<div class="text-center" v-if="data && data.imgs">
					<block v-for="item in data.imgs" :key="item.id">
						<img  class="my-2 w-c45" :src="$g_img+item" />
					</block>
				</div>
		</div>
	</view>
</template>

<script setup>
import { onLoad } from '@dcloudio/uni-app'; 
import {apiDetailData,apiDetailDataByType } from "@/api/index.js"
import content from '@/components/diy/content/index.vue';

onLoad((options)=>{
	if (options.cid >0) {
		getDataByCid(options.cid,options.id);
	}
	if (options.type) {
		getDataByType(options.type,options.id);
	}
	
}) 
const data=ref()
function getDataByCid(cid,id){
	apiDetailData(cid,id).then(res=>{
		data.value=res
	})
}
function getDataByType(type,id){
	apiDetailDataByType(type,id).then(res=>{
		data.value=res
	})
}
</script>

